<template>
    <div>
      <el-col :span="24">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="业务类型">
              <el-select v-model="formInline.value1" placeholder="请选择业务类型" clearable>
                  <el-option label="体检" value="体检"></el-option>
                  <el-option label="照相" value="照相"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker
                  v-model="formInline.value2"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="downList">下载统计表</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <div style="margin: 10px 0;color: red;">注：默认显示本日数据，数据包含体检完成人数、体检未完成人数、中断提交人数等所有体检人数。</div>
      <el-card>
        <el-row><el-col :span="4" :push="20"><el-button @click="modeSwitch">{{modeDeom}}</el-button></el-col></el-row>
        <ve-histogram :data="chartData" v-if="modeClick"></ve-histogram>
         <el-table
            v-else
            :data="chartData.rows"
            style="width: 100%">
            <el-table-column
              prop="设备"
              label="设备编号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="已完成"
              label="已完成"
              width="180">
            </el-table-column>
            <el-table-column
              prop="未完成"
              label="未完成">
            </el-table-column>
            <el-table-column
              prop="总数"
              label="总数">
            </el-table-column>
          </el-table>
      </el-card>
    </div>
</template>
<script>
    import VeHistogram from 'v-charts/lib/histogram'
  	export default {
      data(){
        return{
          chartData:'',
          formInline:{
            value1:'',
            value2:''
          },
          modeDeom:'表格方式',
          modeClick:true
        }
      },
      methods:{
        onSubmit(){
          console.log('查询')
        },
        downList(){
          console.log('下载统计表')
        },
        modeSwitch(){
          if(this.modeClick){
            this.modeClick = false
            this.modeDeom = '图表方式'
          }else{
            this.modeClick = true
            this.modeDeom = '表格方式'
          }
        }
      },
      created () {
          this.chartData = {
            columns: ['设备', '已完成','未完成'],
            rows: [
              { '设备': 'tjj001', '已完成': 1232, '未完成':123,'总数':1355},
              { '设备': 'tjj002', '已完成': 1223 , '未完成':232,'总数':1335},
              { '设备': 'tjj003', '已完成': 2123 , '未完成':443,'总数':1235},
              { '设备': 'tjj004', '已完成': 4123 , '未完成':231,'总数':1545},
              { '设备': 'tjj005', '已完成': 3123 , '未完成':223,'总数':1657},
              { '设备': 'tjj006', '已完成': 1000 , '未完成':121,'总数':1337},
              { '设备': '合计','已完成':11232,'未完成':8678,'总数':17897}
            ]
          }
        },
        components: { VeHistogram }
    }
</script>
